.introMain {
    position: relative;
    padding: 20px 0;
    min-height: 100vh;
    background-image: url('../images/intro-slider/intro-slider-1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    &-title {
        color: #fff;
        font-size: 89px;
        font-weight: 700;
        margin-top: 50px;
    }
}

.introPages {
    background-image: url("../images/intro-slider/intro-2.jpg");
    position: relative;
    padding: 20px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 50vh;
}

.intro-404 {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.intro-404-link {
    font-weight: 500;
    font-size: 22px;
    border-radius: 10px;
    position: relative;
    color: #fff;
    background-color: #1A6EC1;
    padding: 12px;
    transition: .2s ease-in-out;
    &:hover {
        color: #fff;
        background-color: darken($color: #1A6EC1, $amount: 10);
    }
}

.intro-404-img {
    max-width: 700px;
    width: 100%;
    height: auto;
}

.title-404 {
    text-align: left;
    margin-top: 12px !important;
}



@media screen and (max-width: 1400px) {
    .intro-404-img {
        max-width: 400px;
        width: 100%;
        height: auto;
    }
    .introMain {
        padding: 20px 0;
        min-height: 100vh;
        background-image: url('../images/intro-slider/intro-slider-1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        &-title {
            color: #fff;
            font-size: 58px;
            font-weight: 700;
            margin-top: 80px;
            text-align: center;
        }
    }
}

@media screen and (max-width: 1200px) {
    .intro-404-img {
        max-width: 600px;
        width: 100%;
        height: auto;
    }
    .title-404 {
        margin-top: 100px !important;
    }
    .introMain {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px 0;
        min-height: 100vh;
        background-image: url('../images/intro-slider/intro-slider-1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        &-title {

            color: #fff;
            font-size: 58px;
            font-weight: 700;
            text-align: center;
        }
    }
}

@media screen and (max-width: 992px) {
    .introMain {
        &-title {
            color: #fff;
            font-size: 42px;
            font-weight: 700;
            text-align: center;
        }
    }
    .intro-404-link {
        font-size: 20px;
        padding: 10px;
    }
}

@media screen and (max-width: 576px) {
    .introMain {
        align-items: normal;
        &-title {
            color: #fff;
            font-size: 30px;
            font-weight: 700;
            text-align: center;
            margin-top: 230px;
        }
    }
    .intro-404-link {
        font-size: 16px;
        padding: 6px;
    }
}

@media screen and (max-width: 400px) {
    .introMain {
        &-title {
            color: #fff;
            font-size: 26px;
            font-weight: 700;
            text-align: center;
        }
    }
    .intro-404-link {
        font-size: 14px;
        padding: 6px;
    }
}